@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  main {
    flex: none !important;
  }
  .check-in-circle {
    display: block;
  }
  .advantage-card__name,
  .number-card__content-info,
  .number-card__content-title,
  .advantage-cards--list .advantage-card__name,
  .advantage-cards--list .advantage-card__body,
  .number-cards--width-header .number-card__body,
  .number-card__header,
  .number-cards--width-header .number-card__header-title,
  .company-awards__name,
  .advantage-cards--shadow .advantage-card__body,
  .one-click__body,
  .list-check li,
  .main-world__body,
  .headline-feature__info,
  .check-in-circle,
  .check-in-circle li,
  .grid-container--a img {
    max-width: 100%;
  }
  .grid-container--a img {
    width: 100%;
    display: block;
  }
  .terminal-card__info,
  .terminal-cards__cards,
  .best-traders__cards .number-card__content,
  .partners-steps__cards,
  .trading-market__content,
  .main-features__cards {
    width: 100%;
    width: 100%;
  }
  .contact-form__form .h2 {
    margin-bottom: 120px;
  }
  .t-market-control--cfdproducts .t-market-control__name {
    top: 67px;
  }
  .t-market-control--bitcoin .t-market-control__name {
    top: 83px;
  }
}

@supports (-ms-ime-align: auto) {
  .trading-market__content.tab-container {
    width: 100%;
  }
  .t-market-control--cfdproducts .t-market-control__name {
    top: 67px;
  }
  .t-market-control--bitcoin .t-market-control__name {
    top: 83px;
  }
}
